import {Component, OnInit} from '@angular/core';
import {AuthService} from '../../services/auth.service';
import {PageLoaderService} from '../../../ic-core';
import {Router} from '@angular/router';

@Component({
  selector: 'ic-forget-password',
  templateUrl: './forget-password.component.html',
  styleUrls: ['./forget-password.component.scss']
})
export class ForgetPasswordComponent implements OnInit {

  public username: string;
  public errorMessage: string;
  private resetPasswordUrl = '/auth/reset-password';

  constructor(private router: Router, private authService: AuthService, private pageLoaderService: PageLoaderService) {
  }

  ngOnInit() {
  }

  async sendVerificationCode() {
    this.pageLoaderService.showPageLoader();
    const result = await this.authService.forgetPassword(this.username);
    this.pageLoaderService.hidePageLoader();
    if (result.result) {
      this.router.navigate([this.resetPasswordUrl], {queryParams: {username: this.username, email: result.email}});
    } else {
      this.errorMessage = `Error: ${result.error}`;
    }
  }

}
