import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AuthService} from '../../services/auth.service';
import {AlertDialog, PageLoaderService} from '../../../ic-core';
import {MatDialog} from '@angular/material';

@Component({
  selector: 'ic-active-account',
  templateUrl: './active-account.component.html',
  styleUrls: ['./active-account.component.scss']
})
export class ActiveAccountComponent implements OnInit {


  public verificationCode;
  public email;
  public username;
  public password;
  public errorMessage;


  private loginUrl = 'auth/sign-in';

  constructor(private route: ActivatedRoute, private router: Router,
              private authService: AuthService,
              private pageLoaderService: PageLoaderService,
              private dialog: MatDialog) {
  }

  ngOnInit() {
    this.email = this.route.snapshot.queryParams.email;
    this.username = this.route.snapshot.queryParams.username;
    this.verificationCode = this.route.snapshot.queryParams.code || '';
  }

  async resendActiveCode() {
    const routerLink = 'auth/resend-active-code';
    await this.router.navigate([routerLink], {queryParams: {username: this.username, email: this.email}});
  }

  async activeAccount() {
    this.pageLoaderService.showPageLoader();
    const result = await this.authService.activeAccount(this.username, this.verificationCode);
    this.pageLoaderService.hidePageLoader();
    if (result.result) {
      const dialogRef = this.dialog.open(AlertDialog, {
        width: '300px',
        data: `Your Account Has Been Activated!`
      });

      dialogRef.afterClosed().subscribe(async () => {
        await this.router.navigate([this.loginUrl]);
      });

    } else {
      this.errorMessage = `Error: ${result.error}`;
    }
  }
}

