import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AuthService} from '../../services/auth.service';
import {PageLoaderService} from '../../../ic-core';
import {Auth} from 'aws-amplify';

@Component({
  selector: 'ic-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss']
})
export class ChangePasswordComponent implements OnInit {

  public oldPassword;
  public newPassword;
  public errorMessage;
  private loginUrl = 'auth/sign-in';

  constructor(private route: ActivatedRoute,
              private router: Router,
              private authService: AuthService,
              private pageLoaderService: PageLoaderService) {
  }

  ngOnInit() {
  }

  async changePassword() {

    this.pageLoaderService.showPageLoader();
    const result = await this.authService.changePassword(this.oldPassword, this.newPassword);
    this.pageLoaderService.hidePageLoader();
    if (result.result) {
      this.authService.signOut();
      // await this.router.navigate([this.loginUrl]);
    } else {
      this.errorMessage = `Error: ${result.error}`;
    }
  }
}
