import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AuthService} from '../../services/auth.service';
import {PageLoaderService} from '../../../ic-core';
import {Auth} from 'aws-amplify';

@Component({
  selector: 'ic-change-password',
  templateUrl: './complete-new-password.component.html',
  styleUrls: ['./complete-new-password.component.scss']
})
export class CompleteNewPasswordComponent implements OnInit {

  public newPassword;
  public errorMessage;
  private loginUrl = 'auth/sign-in';
  private returnUrl;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private authService: AuthService,
              private pageLoaderService: PageLoaderService) {
  }

  ngOnInit() {
    this.returnUrl = this.route.snapshot.queryParams.returnUrl || '/';
  }

  async changePassword() {

    this.pageLoaderService.showPageLoader();
    const result = await this.authService.completeNewPassword(this.newPassword);
    this.pageLoaderService.hidePageLoader();
    if (result.result) {
      await this.router.navigate([this.returnUrl]);
    } else {
      this.errorMessage = `Error: ${result.error}`;
    }
  }
}
