import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AmplifyService} from 'aws-amplify-angular';
import {AuthService} from '../../services/auth.service';
import {PageLoaderService} from '../../../ic-core';

@Component({
  selector: 'ic-sign-in',
  templateUrl: './sign-in.component.html',
  styleUrls: ['./sign-in.component.scss']
})
export class SignInComponent implements OnInit {

  public username;
  public password;
  public errorMessage;


  private returnUrl: string;
  private completeNewPasswordUrl = 'auth/complete-new-password';


  /*
  private signUpConfig = {
    header: 'Create A New Account',
    usernameAttributes: 'username',
    hideAllDefaults: true,
    defaultCountryCode: '84',
    signUpFields: [
      {
        label: 'User Name',
        key: 'username',
        required: true,
        displayOrder: 1,
        type: 'string',
      },
      {
        label: 'Your Email',
        key: 'email',
        required: true,
        displayOrder: 2,
        type: 'string',
      },
      {
        label: 'Password',
        key: 'password',
        required: true,
        displayOrder: 3,
        type: 'password'
      },
      {
        label: 'Phone Number',
        key: 'phone_number',
        required: true,
        displayOrder: 4,
        type: 'string'
      }
    ]
  };
  */
  constructor(private route: ActivatedRoute, private router: Router,
              private authService: AuthService,
              private pageLoaderService: PageLoaderService) {
  }

  ngOnInit() {
    this.returnUrl = this.route.snapshot.queryParams.returnUrl || '/';
    this.authService.addSignInListener(async (data) => {
      await this.router.navigate([this.returnUrl]);
    });
  }

  async signIn() {
    this.pageLoaderService.showPageLoader();
    const result = await this.authService.signIn(this.username, this.password);
    this.pageLoaderService.hidePageLoader();
    if (result.result) {
      // await this.router.navigate([this.returnUrl]); => ngOnInit()
      if (result.user.challengeName === 'NEW_PASSWORD_REQUIRED') {
        this.authService['NEW_PASSWORD_REQUIRED'] = result.user;
        await this.router.navigate([this.completeNewPasswordUrl], {queryParams: {returnUrl: this.returnUrl}});
      }
    } else {
      this.errorMessage = `Error: ${result.error}`;
    }
  }

}
