import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AmplifyService} from 'aws-amplify-angular';
import {AuthService} from '../../services/auth.service';
import {PageLoaderService} from '../../../ic-core';

@Component({
  selector: 'ic-login',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {

  public verificationCode;
  public email;
  public username;
  public password;
  public errorMessage;


  private loginUrl = 'auth/sign-in';

  constructor(private route: ActivatedRoute, private router: Router,
              private authService: AuthService,
              private pageLoaderService: PageLoaderService) {
  }

  ngOnInit() {
    this.email = this.route.snapshot.queryParams.email;
    this.username = this.route.snapshot.queryParams.username;
    this.verificationCode = this.route.snapshot.queryParams.code || '';
    // if (!!this.username) {
    //   this.router.navigate(['./']);
    // }
  }

  async resetPassword() {
    this.pageLoaderService.showPageLoader();
    const result = await this.authService.resetPassword(this.username, this.verificationCode, this.password);
    this.pageLoaderService.hidePageLoader();
    if (result.result) {
      await this.router.navigate([this.loginUrl]);
    } else {
      this.errorMessage = `Error: ${result.error}`;
    }
  }

}
