import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {AuthContainer} from './auth.container';
import {SignInComponent} from './pages/sign-in/sign-in.component';
import {ForgetPasswordComponent} from './pages/forget-password/forget-password.component';
import {ResetPasswordComponent} from './pages/reset-password/reset-password.component';
import {SignUpComponent} from './pages/sign-up/sign-up.component';
import {AuthGuard} from './guards/auth.guard';
import {ChangePasswordComponent} from './pages/change-password/change-password.component';
import {ActiveAccountComponent} from './pages/active-account/active-account.component';
import {ResendActiveCodeComponent} from './pages/resend-active-code/resend-active-code.component';
import {CompleteNewPasswordComponent} from './pages/complete-new-password/complete-new-password.component';

const routes: Routes = [
  {
    path: '',
    component: AuthContainer,
    children: [
      {
        path: 'sign-up',
        component: SignUpComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'active-account', // confirm code of sign-up
        component: ActiveAccountComponent
      },
      {
        path: 'resend-active-code', // resend-active-code for lost active-account
        component: ResendActiveCodeComponent
      },
      {
        path: 'change-password',
        component: ChangePasswordComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'complete-new-password',
        component: CompleteNewPasswordComponent
      },
      {
        path: 'sign-in',
        component: SignInComponent
      },
      {
        path: 'forget-password',
        component: ForgetPasswordComponent
      },
      {
        path: 'reset-password', // confirm code of forget-password and input new password
        component: ResetPasswordComponent
      },

    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthRoutingModule {
}
