import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Route, Router} from '@angular/router';
import {AuthService} from '../../services/auth.service';
import {PageLoaderService} from '../../../ic-core';

@Component({
  selector: 'ic-resend-active-code',
  templateUrl: './resend-active-code.component.html',
  styleUrls: ['./resend-active-code.component.scss']
})
export class ResendActiveCodeComponent implements OnInit {

  public email;
  public username;
  public errorMessage;
  private returnUrl = 'auth/active-account';

  constructor(private route: ActivatedRoute,
              private router: Router,
              private authService: AuthService,
              private pageLoaderService: PageLoaderService) {
  }

  ngOnInit() {
    this.email = this.route.snapshot.queryParams.email;
    this.username = this.route.snapshot.queryParams.username;
  }

  async resendActiveAccountCode() {
    this.pageLoaderService.showPageLoader();
    const result = await this.authService.resendActiveAccountCode(this.username);
    this.pageLoaderService.hidePageLoader();
    if (result.result) {
      await this.backToActiveAccount();
    } else {
      this.errorMessage = `Error: ${result.error}`;
    }
  }

  async backToActiveAccount() {
    await this.router.navigate([this.returnUrl], {queryParams: {username: this.username, email: this.email}});
  }

}
