import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AmplifyService} from 'aws-amplify-angular';
import {AuthService} from '../../services/auth.service';
import {AlertDialog, ConfirmDialog, PageLoaderService} from '../../../ic-core';
import {MatDialog, MatTableDataSource} from '@angular/material';
import {Customer} from '../../../ic-core';

@Component({
  selector: 'ic-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss']
})
export class SignUpComponent implements OnInit {

  public username;
  public password;
  public email;
  public errorMessage;


  private returnUrl: string;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private authService: AuthService,
              private pageLoaderService: PageLoaderService,
              private dialog: MatDialog) {
  }

  ngOnInit() {
    this.returnUrl = this.route.snapshot.queryParams.returnUrl || '/';
  }

  async signUp() {
    this.pageLoaderService.showPageLoader();
    const result = await this.authService.signUp(this.username, this.email, this.password);
    this.pageLoaderService.hidePageLoader();
    if (result.result) {
      const dialogRef = this.dialog.open(AlertDialog, {
        width: '300px',
        data: `Account Has Been Created!`
      });

      dialogRef.afterClosed().subscribe(() => {
        this.router.navigate([this.returnUrl]);
      });
    } else {
      this.errorMessage = `Error: ${result.error}`;
    }
  }

  async cancel() {
    await this.router.navigate([this.returnUrl]);
  }
}
